import { Component, ViewChild, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ToastController, IonSlides, IonSlide, Platform, NavController, IonContent } from '@ionic/angular';
import { UsuariosLoginService } from 'src/app/providers/Api/UsuariosLogin.service';
import { Router } from '@angular/router';
import { RegistroService } from '../../providers/Registro/registro.service';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { Registro } from '../../interfaces/registro.interface';
import { from } from 'rxjs';
import { InicioGuard } from '../../Guard/login/inicio.guard';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';
import { AlertService } from '../../providers/Utilidades/AlertService.service';
import { last, first } from 'rxjs/operators';
import { PushService } from '../../providers/PushService/PushService.service';

@Component({
  selector: 'app-registro',
  templateUrl: './registro.page.html',
  styleUrls: ['./registro.page.scss'],
})
export class RegistroPage implements OnInit{
  [x: string]: any;

  forma: FormGroup;
  usuarioValido: boolean = true;
  colorStatus = 'blanco';
  mensaje = 'Ingresa su nombre completo';
  usuarioID: any = '';
  paises= [];
  ciudades = [];
  ciudad = false;
  load = false;

  pais = '';
  miciudad = '';

  index = 0;

  @ViewChild('slide', {static: false}) Slide: IonSlides;
  @ViewChild('content') Content: IonContent;

  constructor(
    private usuarioService: UsuariosLoginService,
    private toastService: ToastServices,
    private router: Router,
    private navCtrl: NavController,
    private registroService: RegistroService,
    private almacenamiento: InicioService,
    private platform: Platform,
    private alertService: AlertService,
    private OSSignal: PushService
    ) {

      this.forma = new FormGroup({
        'nombreCompleto': new FormControl('', [Validators.required, Validators.minLength(10)]),
        'nombreUsuario' : new FormControl('', [Validators.required, Validators.minLength(5)]),
        'correo'        : new FormControl('', [Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]),
        'clave'         : new FormControl('', [Validators.required, Validators.minLength(8)]),
        'confirmarClave': new FormControl(),
        'tel'           : new FormControl('', [Validators.required]),
      });

      this.forma.controls['confirmarClave'].setValidators([
        Validators.required, Validators.minLength(8), this.compareClaves.bind( this.forma )
      ])

      this.OSSignal.$usuarioID.subscribe((usuarioID: any) => {
        this.usuarioID = usuarioID;
        // console.log('Entramos al usuarioID ', usuarioID);
      });

      

   }

   ngOnInit() {

   }

   ionViewWillEnter() {

    this.probar();
    this.listarPaises();
   }

   listarPaises() {
     this.usuarioService.listarPaises().subscribe((data: any) => {
       if (data.exe) {
         this.paises = data.response;
       } else {
         this.paises.push('Colombia');
       }
     }, (err) => {
       this.paises.push('Colombia');
     })
   }


   listarCiudades( event ) {
     this.ciudad= true;
     this.pais =  event.detail.value.nombre;
     this.miciudad = '';
     this.usuarioService.listarCiudades(event.detail.value._id).subscribe((data: any) => {
      if (data.exe) {
        this.ciudades = data.response;
        this.changeOne2();
      } else {
        this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
      }
    }, (err) => {
      this.toastService.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
    })
  }

  seleccionarCiudad( event ) {
    this.miciudad = event.detail.value;
    this.changeOne2();
  }

   async probar() {
     this.load = true;
     const info = await this.almacenamiento.obtenerToken();

     if ( info ) {
       
       this.Slide.lockSwipes(true).then(() => {
        // console.log('bien');
      });
     }
   }

   async changeOne2() {
    // console.log(this.index);
    
    if (this.index === 1 && this.pais !== '' && this.miciudad !== '') {
      this.colorStatus = 'success';
      } else if (this.index !== 1) {
        this.colorStatus = 'success';
      } else {
        this.colorStatus = 'blanco';
      }
      
  }

  async changeOne(campoForma: any[]) {
    // console.log(this.index);
    const item = campoForma.filter((dato: any) => dato === null);
    if (item.length === campoForma.length) {
      if (this.index === 1 && this.pais !== '' && this.miciudad !== '') {

      } else if (this.index !== 1) {
        this.colorStatus = 'success';
      } else {
        this.colorStatus = 'blanco';
      }
      
    } else {
      this.colorStatus = 'blanco';
    }
  }


  retroceso() {
    this.alertService.alertBasicApplication('Mensaje', '¿Estas seguro de que deseas cancelar el registro?', () => {
          this.registroService.cambioEstado(true);
          this.navCtrl.navigateBack('/home');
    })
  }
  // Validaciones Funcines

  compareClaves(control: FormControl): {[s: string]:boolean} {
    if (control.value !== this.controls['clave'].value) {
      return {
        compare: true
      }
    }
    return null;
  }

  guardarCambios() {
    this.usuarioValido = false;

    const dataRegistro: any = this.forma.value;
    dataRegistro.usuarioID = this.usuarioID === '' ? 'hola' : this.usuarioID;

    dataRegistro.pais = this.pais,
    dataRegistro.ciudad = this.miciudad;
    this.usuarioService.registrar(dataRegistro).subscribe(
      (datos) => {
         this.usuarioValido = true;
         delete datos.clave;
         datos.clave = this.forma.controls.clave.value;
         datos.usuarioID = 'hola';

         this.almacenamiento.guardarUsuario(datos).then(() => {
           this.toastService.alertApplication('Usuario registrado corectamente', true);

           this.registroService.cambioEstado(true);
           this.almacenamiento.cambiarEstado(false);
           this.navCtrl.navigateForward(['/central']);

         }, (error) => {
           this.usuarioValido = true;
           this.toastService.alertApplication(error, false);
         });

        },
      (error) => { this.toastService.alertApplication(error, false); this.usuarioValido = true;},
      () => { }
    );
  }

  comprobarUsuario(event) {
    if (!this.forma.controls.nombreUsuario.errors){
      this.usuarioValido = false;
      this.usuarioService.usuario(event.detail.value).subscribe(async (data) => {
     
      if (data.exe) {
        this.usuarioValido = true;
        this.colorStatus = 'blanco';
        this.mensaje = 'El usuario ya existe, por favor escoge otro nombre';

      } else {
        this.colorStatus = 'success';
        this.usuarioValido = true;
        this.mensaje = 'El nombre de usuario esta disponible';
      }
    }, async (error) => {
      await this.toastService.alertApplication(error, false);
      this.usuarioValido = true;
    }, () => {
      // console.log('Listo Completado');
    })
      
    } else {
      this.colorStatus = 'blanco';
    }
    
  }


   async next() {
     // console.log('Next');
     if (this.colorStatus === 'success') {
        await this.Content.scrollToTop(100);
        this.Slide.getActiveIndex().then((index) => {
            this.index = index;
            if (index === 3) {
              this.guardarCambios();
              return;
            } else if (index === 0) {
              this.mensaje = 'Escge un nombre de usuario personalizado';
            }  else if (index === 1) {
              this.mensaje = 'Ingresa un correo y número celular válido al cual se contacten contigo';
            } else if (index === 2) {
              this.mensaje = 'Tú contraseña debe tener almenos 8 caracteres';
            }
            this.avanzar();
        });
     }
  }

  async avanzar() {
    // console.log('Avanzar');
    this.colorStatus = 'blanco';
    this.registroService.cambioEstado(false);
    await this.Slide.lockSwipes(false);
    await this.Slide.lockSwipeToPrev(true);
    await this.Slide.slideNext(300);
    await this.Slide.lockSwipeToNext(true);
  }

  ioniewDidLeave() {
    this.Slide.lockSwipes(true).then(() => {
      // console.log('bien');
    });
  }



}
